body {
  font-family: Arial, Helvetica, sans-serif;
  height: 100vh;
  transition: all 0.3s;
  padding: 10px;
}

img {
  border-radius: 10px;
}

#app {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 30px;
}

h1 {
  font-size: 30px;
  color: rgb(105, 16, 16);
}

* {
  margin: 0;
  padding: 0;
  text-align: center;
  box-sizing: border-box;
}

.addToBasket {
  display: flex;
  align-items: center;
  gap: 10px;
}

.addToBasket__btn {
  width: 40px;
  height: 40px;
  border: none;
  font-size: 30px;
  border-radius: 5px;
  color: rgb(255, 244, 142);
  cursor: pointer;
}

.plus {
  background-color: green;
}

.minus {
  background-color: red;
}

.addToBasket__count {
  font-size: 30px;
  background-color: white;
  width: 120px;
}

.btnAdd {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: blue;
  color: rgb(255, 244, 142);
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
}

.btnAdd:hover {
  background-color: rgb(3, 3, 209);
}

.card {
  padding: 10px;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  background-color: rgb(255, 244, 142);
  border-radius: 20px;
}

.card__price {
  font-size: 40px;
  font-weight: bold;
}

#menu {
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__list {
  display: flex;
  list-style: none;
}

.menu__listItem {
  padding: 5px 20px;
  font-size: 25px;
  cursor: pointer;
  border: 3px solid white;
}

.menu__listItem:hover {
  border-bottom: 3px solid black;
  border-radius: 10px;
}

.scaled:hover {
  transform: scale(150%);
}

.basket__btn {
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 20px;
  color: white;
  background-color: rgb(67, 196, 67);
  cursor: pointer;
}

.basket__btn:hover {
  background-color: rgb(35, 173, 35);
}

.basket {
  position: absolute;
  top: 20px;
  right: 20px;
  background-color: rgb(212, 221, 252);
  font-weight: bold;
  border-radius: 20px;
  padding: 15px;
  visibility: hidden;
}

thead td {
  font-size: 30px;
}

td {
  font-size: 20px;
  border: 1px solid black;
  padding: 10px 8px;
}

.sum {
  margin: 20px 0 20px 0;
  font-size: 25px;
}

.closeBasket {
  position: absolute;
  bottom: 10px;
  right: 10px;
  background-color: red;
}

.closeBasket:hover {
  background-color: rgb(218, 15, 15);
}

.disabled {
  opacity: 0.5;
}

.btnDel {
  background-color: red;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 20px;
  color: white;
  cursor: pointer;
}

.btnDel:hover {
  background-color: rgb(218, 15, 15);
}
